// utils / string

import {
  isArray,
  isString,
  isNumber,
  hasProperty
} from './detection.js'

import { randint } from './random.js'

const toString = (input) => {
  const s = isNumber(input) ? String(input) : input
  if (!isString(s)) {
    throw new Error('InvalidInput: String required.')
  }
  return s
}

export const truncate = (s, l) => {
  const o = toString(s)
  const t = l || 140
  if (o.length <= t) {
    return o
  }
  let x = o.substring(0, t)
  const a = x.split(' ')
  const b = a.length
  let r = ''
  if (b > 1) {
    a.pop()
    r += a.join(' ')
    if (r.length < o.length) {
      r += '...'
    }
  } else {
    x = x.substring(0, t - 3)
    r = x + '...'
  }
  return r
}

export const stripTags = (s) => {
  return toString(s)
    .replace(/<.*?>/gi, ' ')
    .replace(/\s\s+/g, ' ')
    .trim()
}

export const escapeHTML = (s) => {
  const x = toString(s)
  return x.replace(/&/g, '&amp;')
    .replace(/</g, '&lt;')
    .replace(/>/g, '&gt;')
    .replace(/"/g, '&quot;')
}

export const unescapeHTML = (s) => {
  const x = toString(s)
  return x.replace(/&quot;/g, '"')
    .replace(/&lt;/g, '<')
    .replace(/&gt;/g, '>')
    .replace(/&amp;/g, '&')
}

export const ucfirst = (s) => {
  const x = toString(s).toLowerCase()
  return x.length > 1 ? x.charAt(0).toUpperCase() + x.slice(1) : x.toUpperCase()
}

export const ucwords = (s) => {
  return toString(s).split(' ').map((w) => {
    return ucfirst(w)
  }).join(' ')
}

export const replaceAll = (s, a, b) => {
  let x = toString(s)

  if (isNumber(a)) {
    a = String(a)
  }
  if (isNumber(b)) {
    b = String(b)
  }

  if (isString(a) && isString(b)) {
    const aa = x.split(a)
    x = aa.join(b)
  } else if (isArray(a) && isString(b)) {
    a.forEach((v) => {
      x = replaceAll(x, v, b)
    })
  } else if (isArray(a) && isArray(b) && a.length === b.length) {
    const k = a.length
    if (k > 0) {
      for (let i = 0; i < k; i++) {
        const aaa = a[i]
        const bb = b[i]
        x = replaceAll(x, aaa, bb)
      }
    }
  }
  return x
}

export const stripAccent = (s) => {
  let x = toString(s)

  const map = {
    a: '|||||||||||||||||',
    A: '|||||||||||||||||',
    c: '',
    C: '',
    d: '',
    D: '',
    e: '|||||||||||',
    E: '|||||||||||',
    i: '||||||',
    I: '||||||',
    o: '|||||||||||||||||',
    O: '|||||||||||||||||',
    u: '|||||||||||',
    U: '|||||||||||',
    y: '||||',
    Y: '||||'
  }

  const updateS = (ai, key) => {
    x = replaceAll(x, ai, key)
  }

  for (const key in map) {
    if (hasProperty(map, key)) {
      const a = map[key].split('|')
      a.forEach((item) => {
        return updateS(item, key)
      })
    }
  }
  return x
}

const getCharList = () => {
  const lowerChars = 'abcdefghijklmnopqrstuvwxyz'
  const upperChars = lowerChars.toUpperCase()
  const digits = '0123456789'
  const characters = [
    lowerChars,
    upperChars,
    digits
  ].join('').split('')
  return characters
}

export const genid = (leng, prefix = '') => {
  const chars = getCharList().sort(() => {
    return Math.random() > 0.5
  }).join('')
  const t = chars.length
  const ln = Math.max(leng || 32, prefix.length)
  let s = prefix
  while (s.length < ln) {
    const k = randint(0, t)
    s += chars.charAt(k) || ''
  }
  return s
}

export const slugify = (s, delimiter = '-') => {
  return stripAccent(s)
    .trim()
    .toLowerCase()
    .replace(/\W+/g, ' ')
    .replace(/\s+/g, ' ')
    .replace(/\s/g, delimiter)
}
